<script setup>
import { ref, onMounted, onBeforeUnmount, nextTick } from "vue";

const showNavbar = ref(false);
const hero = ref(null);
const snapContainer = ref(null);

function handleScroll() {
    if (!snapContainer.value || !hero.value) return;
    const scrollTop = snapContainer.value.scrollTop;
    const heroHeight = hero.value.offsetHeight;
    showNavbar.value = scrollTop > heroHeight - 50;
}

function scrollTo(id) {
    const container = snapContainer.value;
    if (!container) return;
    const target = container.querySelector(`#${id}`);
    if (target) {
        // scroll the container to the target's offsetTop
        container.scrollTo({ top: target.offsetTop, behavior: "smooth" });
    }
}

onMounted(async () => {
    await nextTick();
    if (snapContainer.value) {
        snapContainer.value.addEventListener("scroll", handleScroll, { passive: true });
    } else {
        window.addEventListener("scroll", handleScroll, { passive: true });
    }
});

onBeforeUnmount(() => {
    if (snapContainer.value) {
        snapContainer.value.removeEventListener("scroll", handleScroll);
    } else {
        window.removeEventListener("scroll", handleScroll);
    }
});
</script>

